import React from "react"
import Link from "gatsby-link"

export default function Template({data}) {
  const { markdownRemark } = data
  const { frontmatter, html } = markdownRemark
  return (
    <div className="content-wrapper">
      <div className="row">
        <div className="columns medium-12">
          <section className="nameCategory">
            <div className="card-caption large">
              <div className="card-caption__img-wrapper">
                <img src={frontmatter.icon} alt="Выдача ордера" />
              </div>
              <h4 className="card-caption__name">{frontmatter.title}</h4>
            </div>
            <Link to="/uslugi">
              <button type="button" className="button button-lg button-arrow-left">Все услуги</button>
            </Link>
          </section>
        </div>
        <div className="columns medium-9">
          <div dangerouslySetInnerHTML={{ __html: html }}/>
          <section className="titleStep">
            <ul className="titleStep__list transportAndDriving">
              <li className="list__caption">{frontmatter.captionTitle}</li>
              {frontmatter.captionText &&
                <li className="list__content">У Вас возникла необходимость в получении муниципальной услуги</li>
              }
            </ul>
          </section>
        </div>
        <div className="columns medium-9">
          <div className="service">
            <div className="steps-wrapper">
              {frontmatter.listItemCaption1 &&
                <ul className="steps transportAndDriving">
                  <li className="steps__caption"><span>1</span>{frontmatter.listItemCaption1}</li>
                  {frontmatter.listItemText1 &&
                    <li className="steps__content">
                      <div>{frontmatter.listItemText1}</div>
                    </li>
                  }
                </ul>
              }
              {frontmatter.listItemCaption2 &&
                <ul className="steps transportAndDriving">
                  <li className="steps__caption"><span>2</span>{frontmatter.listItemCaption2}</li>
                  {frontmatter.listItemText2 &&
                  <li className="steps__content">
                    <div>{frontmatter.listItemText2}</div>
                  </li>
                  }
                </ul>
              }
              {frontmatter.listItemCaption3 &&
                <ul className="steps transportAndDriving">
                  <li className="steps__caption"><span>3</span>{frontmatter.listItemCaption3}</li>
                  {frontmatter.listItemText3 &&
                  <li className="steps__content">
                    <div>{frontmatter.listItemText3}</div>
                  </li>
                  }
                </ul>
              }
              {frontmatter.listItemCaption4 &&
                <ul className="steps transportAndDriving">
                  <li className="steps__caption"><span>4</span>{frontmatter.listItemCaption4}</li>
                  {frontmatter.listItemText4 &&
                  <li className="steps__content">
                    <div>{frontmatter.listItemText4}</div>
                  </li>
                  }
                </ul>
              }
              {frontmatter.listItemCaption5 &&
                <ul className="steps transportAndDriving">
                  <li className="steps__caption"><span>5</span>{frontmatter.listItemCaption5}</li>
                  {frontmatter.listItemText5 &&
                  <li className="steps__content">
                    <div>{frontmatter.listItemText5}</div>
                  </li>
                  }
                </ul>
              }
              {frontmatter.listItemCaption6 &&
                <ul className="steps transportAndDriving">
                  <li className="steps__caption"><span>6</span>{frontmatter.listItemCaption6}</li>
                  {frontmatter.listItemText6 &&
                  <li className="steps__content">
                    <div>{frontmatter.listItemText6}</div>
                  </li>
                  }
                </ul>
              }
              {frontmatter.listItemCaption7 &&
                <ul className="steps transportAndDriving">
                  <li className="steps__caption"><span>7</span>{frontmatter.listItemCaption7}</li>
                  {frontmatter.listItemText7 &&
                  <li className="steps__content">
                    <div>{frontmatter.listItemText7}</div>
                  </li>
                  }
                </ul>
              }
              {frontmatter.lastItemCaption &&
                <ul className="lastStep transportAndDriving">
                  <li className="lastStep__caption"><span></span>{frontmatter.lastItemCaption}</li>
                  {frontmatter.lastItemText &&
                    <li className="lastStep__content">{frontmatter.lastItemText}</li>
                  }
                </ul>
              }
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export const pageQuery = graphql`
  query BlogPostByPath($path: String!) {
    markdownRemark(frontmatter: { path: { eq: $path } }) {
      html
      frontmatter {
        date(formatString: "MMMM DD, YYYY")
        icon
        title
        captionTitle
        captionText
        listItemCaption1
        listItemText1
        listItemCaption2
        listItemText2
        listItemCaption3
        listItemText3
        listItemCaption4
        listItemText4
        listItemCaption5
        listItemText5
        listItemCaption6
        listItemText6
        listItemCaption7
        listItemText7
        lastItemCaption
        lastItemText
      }
    }
  }
`;


